          alt="icon"
        />粤通卡 · 清分结算系统
      </p>
    </div>

    <!-- 显示背景的图片和公告栏 -->
    <div class="background">
      <!-- 公告栏 -->
      <div class="info-panel">
        <div class="info-panel-header"></div>

        <div class="info-panel-container-left"></div>

        <div class="info-panel-container-right">
          <div class="info-panel-item"></div>
          <div class="info-panel-item"></div>
          <div class="info-panel-item"></div>
        </div>
      </div>

      <!-- 公告栏上浮动的信息框 -->
      <div class="info-box-container">
        <!-- 红色的信息框，顺序从上到下 -->
        <div class="info-box bg-red">
          <p class="info-box-text">通信监控</p>
          <div class="arrow bg-red"></div>
        </div>
        <div class="info-box bg-red">
          <p class="info-box-text">记账异常管理</p>
          <div class="arrow bg-red"></div>
        </div>
        <div class="info-box bg-red">
          <p class="info-box-text">清分管理</p>
          <div class="arrow bg-red"></div>
        </div>

        <!-- 绿色的信息框，顺序从上到下 -->
        <div class="info-box bg-green">
          <p class="info-box-text">清算监控</p>
          <div class="arrow bg-green"></div>
        </div>
        <div class="info-box bg-green">
          <p class="info-box-text">跨省争议管理</p>
          <div class="arrow bg-green"></div>
        </div>
        <div class="info-box bg-green">
          <p class="info-box-text">人工调账</p>
          <div class="arrow bg-green"></div>
        </div>
        <div class="info-box bg-green">
          <p class="info-box-text">划账管理</p>
          <div class="arrow bg-green"></div>
        </div>
        <div class="info-box bg-green">
          <p class="info-box-text">参数管理</p>
          <div class="arrow bg-green"></div>
        </div>
      </div>

      <!-- 背景山川和人物的图片 -->
      <div class="background-image">
        <img class="image-people" src="../assets/people.png" alt="people" />

        <img
          class="image-mountain"
          src="../assets/mountain.png"
          alt="mountain image"
        />
      </div>

      <form class="input-frame">
        <p class="input-frame-title">用户登录</p>

        <!-- 用户名输入框，被选中时隐藏输入错误提示 -->
        <input
          class="input-field"
          id="username_input"
          type="text"
          placeholder="请输入账号"
          @focus="showInputHint('')"
          @keypress="loginByKeyEnter"
          v-model="username"
        />

        <!-- 密码输入框，被选中时隐藏输入错误提示 -->
        <input
          class="input-field"
          id="password_input"
          type="password"
          placeholder="请输入密码"
          @focus="showInputHint('')"
          @keypress="loginByKeyEnter"
          v-model="password"
        />

        <div class="input-hint-container">
          <div class="input-hint" id="input_hint" v-if="inputHint !== ''">
            <span class="input-hint-text">{{ inputHint }}</span>
          </div>
        </div>

        <button class="input-login bg-green" type="button" @click="login">
          登录
        </button>
      </form>
    </div>

    <div class="bottom-bar"></div>
  </div>
</template>

<script>
export default {
  name: "LoginPage",
  data() {
    return {
      username: "",
      password: "",
      inputHint: "",
    };
  },
  methods: {
    loginByKeyEnter(event) {
      if (event.key === "Enter") {
        this.login();
      }
    },

    login() {
      // username: admin
      // password: somSECR1mCjLNb0YxXR8kQ==

      if (this.username.length === 0) {
        this.showInputHint("请输入用户名");
      } else if (this.password.length === 0) {
        this.showInputHint("请输入密码");
      } else {
        const axios = require("axios");

        axios
          .post("http://128.8.50.55:8080/oauth/login", {
            username: this.username,
            password: this.password,
            key: "",
            code: "",
            loginMode: 0,
          })
          .then((result) => {
            switch (result.data.code) {
              case 200:
                this.gotoHomePage();
                break;

              case 400:
                this.showInputHint(result.data.msg);
                break;
            }
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },

    gotoHomePage() {
      this.$router.go("/home");
    },

    showInputHint(hint) {
      this.inputHint = hint;
    },

    hideInputHint() {
      this.inputHint = "";
    },
  },
};
</script>

